.images-wrapper{
  width: 85%;
  margin: 0 auto;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack:center;
      justify-content:center;
  z-index: 99;
}
.rotate-content{
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  -webkit-transform-style: preserve-3d;
  -webkit-transform: translateX(-45px) translateY(65px) rotateY(50deg) scale(0.7);
  -moz-transform-style: preserve-3d;
  -moz-transform: translateX(-45px) translateY(65px) rotateY(50deg) scale(0.7);
  transform-style: preserve-3d;
  transform: translateX(-45px) translateY(65px) rotateY(50deg) scale(0.7);
}

.sing-image_wrapper{
  width: 180px;
  margin: 20px 10px;
  position: relative;
  cursor: pointer;
}
.image{
  width: 100%;
}
.menu-wrapper{

}
.line{
  height: 3px;
  border-radius: 5px;
  background: #fff;
  margin: 10px;
  position: relative;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
 
}
.line1{
  width: 38px;
}
.line2{
  width: 25px;
  margin-left: 12px;
  background: red;
}
.line3{
  width: 33px;
}
.move-line:nth-child(1){
  -webkit-transform: rotateZ(90deg);
      -ms-transform: rotate(90deg);
          transform: rotateZ(90deg);
  position: relative;
  left: -13px;
  bottom: -11px;
}
.move-line:nth-child(2){

}
.move-line:nth-child(3){
  margin-left: 10px;
  margin-top: -26px;
}
nav{
  width: 40%;
  right: 0;
  position: absolute;
  overflow: hidden;

}
nav::after{
  content: '';
  display: block;
  clear: both;
}
.nav-links{
  text-align: center;
  padding: 0;
}
.nav-link{
  color: #fff;
  font-size: 44px;
  list-style: none;
  margin: 20px auto;
  cursor: pointer;
}
.nav-link:hover{
  color: #b9b9b9;
}
.menu{
  position: absolute;
  right: 0px;
  top: 0px;
  margin: 10px;
  height: 50px;
}
.menu-wrapper{
  margin-top: 90px;
}
.open{
  -webkit-animation: openMenu 1s forwards;
          animation: openMenu 1s forwards;
}
.close{
  -webkit-transition:1s;
  -o-transition:1s;
  transition:1s;
  -webkit-animation: closeMenu 1s forwards;
          animation: closeMenu 1s forwards;
}
@-webkit-keyframes openMenu {
  from{display: none; margin-left: 100%}
    to{display: block;margin-left: 0%}
}
@keyframes openMenu {
  from{display: none; margin-left: 100%}
    to{display: block;margin-left: 0%}
}
@-webkit-keyframes closeMenu {
  from{display: block; margin-left: 0%}
    to{display: none;margin-left: 100%}
}
@keyframes closeMenu {
  from{display: block; margin-left: 0%}
    to{display: none;margin-left: 100%}
}
.full-image_wrapper{
  max-width: 65%;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  -webkit-animation: viewImage 1s forwards;
          animation: viewImage 1s forwards;
  z-index: 3;
}

@-webkit-keyframes viewImage {
  from{width: 10%}
  to{width:50%}
}

@keyframes viewImage {
  from{width: 10%}
  to{width:50%}
}
.close-full_image{
  color: #fff;
  cursor: pointer;
  position: absolute;
  top: -30px;
  right: -22px;
  font-size: 25px;
}
.full-image{
  width: 100%;
}
.next, .prev{
  color:#fff;
  background:rgba(0,0,0,0.5);;
  padding: 20px;
  position: absolute;
  top: 50%;
  cursor: pointer;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.next{
  right: 0;

}
.prev{
  left: 0;
}
.next:hover, .prev:hover{
  background:rgba(0,0,0,0.8);
}
.container{
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  -webkit-perspective: 1000px;
  perspective: 1000px;
}
.filter{
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  filter: brightness(20%);
  -webkit-filter: brightness(20%);
}

@media screen and (max-width:934px) {
  .full-image_wrapper{
    max-width: 90%;
  }
  @-webkit-keyframes viewImage {
    from{width: 10%}
    to{width:100%}
  }
  @keyframes viewImage {
    from{width: 10%}
    to{width:100%}
  }
  .rotate-content {
    -webkit-transform: translateX(-190px) translateY(-65px) rotateY(45deg) scale(0.6);
    transform:  translateX(-190px) translateY(-65px) rotateY(45deg) scale(0.6);
  }
}

@media screen and (max-width:800px) {
  .rotate-content {
    -webkit-transform: translateX(-135px) translateY(-65px) rotateY(45deg) scale(0.6);
    transform:  translateX(-135px) translateY(-65px) rotateY(45deg) scale(0.6);
  }
}
@media screen and (max-width:722px) {
  .rotate-content {
    -webkit-transform:  translateX(-145px) translateY(-190px) rotateY(45deg) scale(0.6);
    transform: translateX(-145px) translateY(-190px) rotateY(45deg) scale(0.6);
  }
  .nav-link {
    font-size: 36px;
  }
}
@media screen and (max-width:488px) {
  nav{
    width: 100%;
  }
  .full-image_wrapper{
    max-width: 98%;
  }
  .rotate-content {
    -webkit-transform: translateZ(0px) rotateY(-15deg);
    -ms-transform: translateY(0px) rotate(-15deg);
        transform: translateY(0px) rotateZ(-15deg)
  }
}



